import { EyeInvisibleOutlined, EyeOutlined, LockOutlined, MailOutlined } from "@ant-design/icons";
import { yupResolver } from '@hookform/resolvers/yup';
import { createNoti } from "components/Notification";
import rpc from "rage-rpc";
import React, { useState } from "react";
import ReactDOM from "react-dom";
import { useForm } from "react-hook-form";
import * as yup from "yup";

/*eslint no-useless-escape: "off"*/
const emailRegex = /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
const passwordRegex = /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{8,}$/;

const schema = yup.object().shape({
  email: yup.string()
    .required("Vui l√≤ng nh·∫≠p email")
    .matches(emailRegex, "Email kh√¥ng h·ª£p l·ªá"),
  password: yup.string()
    .required("Vui l√≤ng nh·∫≠p m·∫≠t kh·∫©u")
    .matches(passwordRegex, "M·∫≠t kh·∫©u c·∫ßn √≠t nh·∫•t 8 k√≠ t·ª±, 1 ch·ªØ hoa v√† 1 ch·ªØ th∆∞·ªùng"),
  repassword: yup.string()
    .required("Vui l√≤ng nh·∫≠p m·∫≠t kh·∫©u")
    .matches(passwordRegex, "M·∫≠t kh·∫©u c·∫ßn √≠t nh·∫•t 8 k√≠ t·ª±, 1 ch·ªØ hoa v√† 1 ch·ªØ th∆∞·ªùng"),
});

function AuthRegister(props) {
  const { setStep, setEmail } = props;
  const [eye1, setEye1] = useState(false);
  const [eye2, setEye2] = useState(false);
  const { register, handleSubmit, errors } = useForm({ resolver: yupResolver(schema) });

  const handleEye1 = () => {
    setEye1(!eye1);
  }
  const handleEye2 = () => {
    setEye2(!eye2);
  }
  const onSubmit = data => {
    const { email, password, repassword } = data;
    if (password !== repassword) {
      createNoti("error", "M·∫≠t kh·∫©u nh·∫≠p l·∫°i kh√¥ng kh·ªõp")
    }
    else {
      const submit_btn = document.getElementById("submit_btn");
      submit_btn.disabled = true;
      rpc.callServer("server:auth.register", { email, password }).then(code => {
        submit_btn.disabled = false;
        switch (code) {
          case 1: {
            createNoti("success", "1 m√£ x√°c minh ƒë√£ ƒë∆∞·ª£c g·ª≠i t·ªõi email üéâ!");
            setTimeout(() => {
              const AuthPage_header = document.getElementById("AuthPage_header");
              ReactDOM.render("X√°c minh email", AuthPage_header);

              setStep(3);
              setEmail(email);
            }, 500);
            break;
          }
          case 2: {
            createNoti("warning", "Email n√†y ƒë√£ ƒë∆∞·ª£c s·ª≠ d·ª•ng üò•!")
            break;
          }
          case 3: {
            createNoti("error", "L·ªói m√°y ch·ªß, vui l√≤ng th·ª≠ l·∫°i sau üò≠!")
            break;
          }
          default: {
            createNoti("error", "L·ªói kh√¥ng x√°c ƒë·ªãnh, vui l√≤ng th·ª≠ l·∫°i sau üò≠!")
            break;
          }
        }
      });
    }
  };

  return (
    <form onSubmit={handleSubmit(onSubmit)} className="form">
      <div className="form_field">
        <span><MailOutlined /></span>
        <input name="email" type="email" className="form_input"
          placeholder="Email"
          ref={register()}
        />
        {
          errors.email?.message &&
          <span className="form_error">
            {errors.email?.message}
          </span>
        }

      </div>

      <div className="form_field">
        <span><LockOutlined /></span>
        <input name="password" type={eye1 ? "text" : "password"} className="form_input form_input_password"
          placeholder="M·∫≠t kh·∫©u"
          ref={register()}
        />
        <span style={{ cursor: "pointer" }} onClick={handleEye1}>
          {eye1 ? <EyeOutlined /> : <EyeInvisibleOutlined />}
        </span>
        {
          errors.password?.message &&
          <span className="form_error">
            {errors.password?.message}
          </span>
        }
      </div>

      <div className="form_field">
        <span><LockOutlined /></span>
        <input name="repassword" type={eye2 ? "text" : "password"} className="form_input form_input_password"
          placeholder="Nh·∫≠p l·∫°i m·∫≠t kh·∫©u"
          ref={register()}
        />
        <span style={{ cursor: "pointer" }} onClick={handleEye2}>
          {eye2 ? <EyeOutlined /> : <EyeInvisibleOutlined />}
        </span>
        {
          errors.repassword?.message &&
          <span className="form_error">
            {errors.repassword?.message}
          </span>
        }
      </div>

      <div className="form_submit">
        <button className="btn btn-success" id="submit_btn">ƒêƒÉng k√≠</button>
      </div>
    </form>
  );
}

export default AuthRegister;