import { BarcodeOutlined } from "@ant-design/icons";
import { yupResolver } from '@hookform/resolvers/yup';
import { createNoti } from "components/Notification";
import rpc from "rage-rpc";
import React, { useEffect, useState } from "react";
import { useForm } from "react-hook-form";
import * as yup from "yup";

/*eslint no-useless-escape: "off"*/

const schema = yup.object().shape({
  verify: yup.string()
    .required("Vui l√≤ng nh·∫≠p m√£ x√°c nh·∫≠n").length(9, "M√£ x√°c minh kh√¥ng h·ª£p l·ªá !")
});

function AuthRegisterVerify(props) {
  const { handleChangeStep, email } = props;
  const [resendAvailable, setResendAvailable] = useState(true);
  const { register, handleSubmit, errors } = useForm({ resolver: yupResolver(schema) });

  let disableTimer;

  const onSubmit = data => {
    const { verify } = data;
    const submit_btn = document.getElementById("submit_btn");
    submit_btn.disabled = true;
    rpc.callServer("server:auth.registerVerify", { email, verify }).then(code => {
      submit_btn.disabled = false;
      switch (code) {
        case 1: {
          createNoti("success", "X√°c minh th√†nh c√¥ng, ƒëƒÉng nh·∫≠p n√†o üéâ!");
          setTimeout(() => {
            handleChangeStep(3, 1);
          }, 500)
          break;
        }
        case 2: {
          createNoti("warning", "M√£ x√°c minh kh√¥ng ch√≠nh x√°c üò•!")
          break;
        }
        case 3: {
          createNoti("error", "L·ªói m√°y ch·ªß, vui l√≤ng th·ª≠ l·∫°i sau üò≠!")
          break;
        }
        default: {
          createNoti("error", "L·ªói kh√¥ng x√°c ƒë·ªãnh, vui l√≤ng th·ª≠ l·∫°i sau üò≠!")
          break;
        }
      }
    })
  };

  const handleResend = () => {
    if (!resendAvailable) {
      createNoti("warning", "B·∫°n v·ª´a g·ª≠i 1 email, h√£y ƒë·ª£i 30 gi√¢y !");
      return;
    }
    else {
      rpc.callServer("server:auth.sendMailVerify", email).then(code => {
        switch (code) {
          case 1: {
            createNoti("success", "1 m√£ x√°c minh ƒë√£ ƒë∆∞·ª£c g·ª≠i t·ªõi email üéâ!");
            break;
          }
          case 2: {
            createNoti("warning", "Kh√¥ng th·ªÉ g·ª≠i email l√∫c n√†y, th·ª≠ l·∫°i sau üò•!")
            break;
          }
          default: {
            createNoti("error", "L·ªói kh√¥ng x√°c ƒë·ªãnh, vui l√≤ng th·ª≠ l·∫°i sau üò≠!")
            break;
          }
        }
      })
      setResendAvailable(false);
      disableTimer = setTimeout(() => {
        setResendAvailable(true);
      }, 30000);
    }
  }

  useEffect(() => {
    return () => {
      clearTimeout(disableTimer);
    }
  }, [disableTimer])

  return (
    <div id="AuthRegisterVerify">
      <span id="AuthPage_resend" onClick={handleResend}>
        G·ª≠i l·∫°i email
      </span>
      <form onSubmit={handleSubmit(onSubmit)} className="form">
        <div className="form_field">
          <span><BarcodeOutlined /></span>
          <input name="verify" type="text" className="form_input"
            placeholder="M√£ x√°c nh·∫≠n"
            ref={register()}
          />
          {
            errors.verify?.message &&
            <span className="form_error">
              {errors.verify?.message}
            </span>
          }

        </div>

        <div className="form_submit">
          <button className="btn btn-success" id="submit_btn">X√°c nh·∫≠n</button>
        </div>
      </form>
    </div>
  );
}

export default AuthRegisterVerify;