import { BarcodeOutlined } from "@ant-design/icons";
import { yupResolver } from '@hookform/resolvers/yup';
import { createNoti } from "components/Notification";
import rpc from "rage-rpc";
import React from "react";
import ReactDOM from "react-dom";
import { useForm } from "react-hook-form";
import * as yup from "yup";

/*eslint no-useless-escape: "off"*/

const schema = yup.object().shape({
  verify: yup.string()
    .required("Vui l√≤ng nh·∫≠p m√£ x√°c nh·∫≠n").length(9, "M√£ x√°c minh kh√¥ng h·ª£p l·ªá !")
});

function AuthForgotVerify(props) {
  const { email, setStep } = props;
  const { register, handleSubmit, errors } = useForm({ resolver: yupResolver(schema) });

  const onSubmit = data => {
    const { verify } = data;
    const submit_btn = document.getElementById("submit_btn");
    submit_btn.disabled = true;
    rpc.callServer("server:auth.forgotVerify", { email, verify }).then(code => {
      submit_btn.disabled = false;
      switch (code) {
        case 1: {
          createNoti("success", "X√°c minh th√†nh c√¥ng, h√£y ƒë·ªïi m·∫≠t kh·∫©u üéâ!");
          setTimeout(() => {
            const AuthPage_header = document.getElementById("AuthPage_header");
            ReactDOM.render("ƒê·ªïi m·∫≠t kh·∫©u", AuthPage_header);

            setStep(6);
          }, 500);
          break;
        }
        case 2: {
          createNoti("warning", "M√£ x√°c minh kh√¥ng ch√≠nh x√°c üò•!")
          break;
        }
        case 3: {
          createNoti("error", "L·ªói m√°y ch·ªß, vui l√≤ng th·ª≠ l·∫°i sau üò≠!")
          break;
        }
        default: {
          createNoti("error", "L·ªói kh√¥ng x√°c ƒë·ªãnh, vui l√≤ng th·ª≠ l·∫°i sau üò≠!")
          break;
        }
      }
    })
  };

  return (
    <div id="AuthForgotVerify">
      <form onSubmit={handleSubmit(onSubmit)} className="form">
        <div className="form_field">
          <span><BarcodeOutlined /></span>
          <input name="verify" type="text" className="form_input"
            placeholder="M√£ x√°c nh·∫≠n"
            ref={register()}
          />
          {
            errors.verify?.message &&
            <span className="form_error">
              {errors.verify?.message}
            </span>
          }
        </div>

        <div className="form_submit">
          <button className="btn btn-success" id="submit_btn">X√°c nh·∫≠n</button>
        </div>
      </form>
    </div>
  );
}

export default AuthForgotVerify;